import { Component, OnInit } from '@angular/core';
import {Apollo, QueryRef} from 'apollo-angular';
import gql from 'graphql-tag';

const instr = gql`
  query {
    party_instr {
      party_ref
      instr_type
      instr_ref_type
      instr_ref
      user_def
      description
  }
}
`;

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'company',
  template: `
    <!-- <app-menubar
      (notifyParentSave)="onSave()"
      (notifyParentRefresh)="onRefresh()"
      (notifyParentDelete)="onDelete()"
      customTitle={{title}}>
    </app-menubar> -->
    <grid [cols]="cols" [rows]="rows"></grid>
  `,
  styles: ['.toolbar-spacer {   flex: 1 1 auto;}']
})

export class CompanyComponent implements OnInit {

  constructor(private apollo: Apollo) {}
  title = 'Company Setup';
  page = 1;
  rows: any[] = [];
  private query: QueryRef<any>;

  cols = [
    {headerName : 'Party Ref',        field: 'party_ref'},
    {headerName : 'Instrument Type',  field: 'instr_type'},
    {headerName : 'Reference Type',   field: 'instr_ref_type'},
    {headerName : 'Reference',        field: 'instr_ref'},
    {headerName : 'User Defined',     field: 'user_def'},
    {headerName : 'Description',      field: 'description'},

  ];

  extractValues(mappings: {}) {
    return Object.keys(mappings);
  }

  lookupValue(mappings: { [x: string]: any; }, key: string | number) {
    return mappings[key];
  }

  lookupKey({ mappings, name }: { mappings: any; name: any; }) {
    const keys = Object.keys(mappings);
    // tslint:disable-next-line: prefer-for-of
    for (let i = 0; i < keys.length; i++) {
      // tslint:disable-next-line: prefer-const
      let key = keys[i];
      if (mappings[key] === name) {
        return key;
      }
    }
  }

  ngOnInit() {
    this.query = this.apollo.watchQuery({
      query: instr,
      variables: { offset: 10 * this.page },
      errorPolicy: 'all'
    });

    this.query.valueChanges.subscribe(result => {
      this.rows = result.data && result.data.party_instr;
    });
  }

update() {
    console.log('Update static page ....');
    this.query.refetch({ offset: 10 * this.page });
  }

nextPage() {
    this.page++;
    this.update();
  }

prevPage() {
    if (this.page > 0) { this.page--; }
    this.update();
  }

onAdd() {
    console.log('Did add');
  }

onDelete() {
    console.log('Did delete');
  }

onRefresh() {
      console.log('Did refresh');
      this.update();
    }
onSave() {
    console.log('Did save');
  }


}
