import { Component, OnInit } from '@angular/core';
import {Apollo, QueryRef} from 'apollo-angular';
import gql from 'graphql-tag';
import { AgGridAngular } from '@ag-grid-community/angular';
import { ClientSideRowModelModule} from '@ag-grid-community/all-modules';


const party = gql`
  query {
    party {
      party_ref
      party_type
      party_long_name
      party_short_name
      party_extra_long_name
      version_no
  }
}
`;

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'party',
  template: `
       <ag-grid-angular
      style="width: 100%; height: 100%;"
      class="ag-theme-alpine-dark"
      [defaultColDef]="defaultColDef"
      [autoGroupColumnDef]="autoGroupColumnDef"
      [enableRangeSelection]="true"
      [animateRows]="true"
      domLayout='autoHeight'
      [rowData]=rows
      [columnDefs]=cols
      (rowClicked)='onRowClicked($event)'
      (cellClicked)='onCellClicked($event)'
      (selectionChanged) = 'onSelectionChanged($event)'
      (cellEditingStarted) = 'onCellEditingStarted($event)'
      [modules]="modules"

      >
  </ag-grid-angular>
  `,
  styles: ['.toolbar-spacer {   flex: 1 1 auto;}']
})

/*
mst16289
mst16288

*/

export class PartyComponent implements OnInit {
  title = 'Party Component';
  page = 1;
  public rows: any[] = [];
  private query: QueryRef<any>;
  public defaultColDef;
  public autoGroupColumnDef;
  private gridApi;
  private gridColumnApi;
  public modules: any[] = [ClientSideRowModelModule];


  public cols = [
    {headerName : 'Party Ref', field: 'party_ref', checkboxSelection: true },
    {headerName : 'Type', field: 'party_type'},
    {headerName : 'Long Name', field: 'party_long_name'},
    {headerName : 'Short Name', field: 'party_short_name'},
    {headerName : 'Full Name', field: 'party_extra_long_name'},
    {headerName : 'Version', field: 'version_no'},
  ];

  constructor(private apollo: Apollo) {}

  onRowClicked(event: any) {
    const data = event.node.data;
    const myJSON = JSON.stringify(data);
    console.log('JSON Object', myJSON);
  }

  onCellClicked(event: any) {
    const data = event.node.data;
    const myJSON = JSON.stringify(data);
    console.log('OnCellClicked: JSON Object', myJSON);
   }

  onSelectionChanged(event: any)
  {
    console.log('onSelectionChanged', event);
    const selectedRow = event.api.getSelectedNode();
    console.log('onSelectionChanged', selectedRow);
  }

  onBtStopEditing() {
    this.gridApi.stopEditing();
  }

  onCellEditingStarted(event: any) {
    console.log('Started editing: now');
  }

  onGridReady(params){
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    params.api.sizeColumnsToFit();
  }

  ngOnInit() {
    this.defaultColDef = {
      flex: 1,
      minWidth: 110,
      filter: true,
      sortable: true,
      resizable: true,
      editable: true,
  };

    this.autoGroupColumnDef = { minWidth: 50 };
    console.log('Auto grid group initialized ... ');
    this.query = this.apollo.watchQuery({
      query: party,
      variables: { offset: 10 * this.page },
      errorPolicy: 'all'
    });

    this.query.valueChanges.subscribe(result => {
      this.rows = result.data && result.data.party;
      console.log('from Party : rows returned');
    });
  }

  update() {
    console.log('Update static page ....');
    this.query.refetch({ offset: 10 * this.page });
  }

  nextPage() {
    this.page++;
    this.update();
  }

  prevPage() {
    if (this.page > 0) { this.page--; }
    this.update();
  }

  onSave() {
    console.log('onSave completed.');
  }

  onAdd() {
    console.log('Did Add');
  }

  onDelete() {
    console.log('Did Add');
  }

  onRefresh() {
      console.log('Did Add');
    }
}
