import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent} from './home/home.component';
import { StaticComponent } from './static/static.component';
import { DaccComponent} from './static/dacc.component';
import { SsipComponent} from './static/ssip/ssip.component';
import { PartyComponent} from './party/party.component';
import { LoginComponent } from './shell/login/login.component';
import { InstrumentsComponent} from './static/instruments.component';
import { CompanyComponent } from './static/company.component';
import { TaskexplorerComponent } from './static/taskexplorer.component';
import { ChargesComponent } from './static/charges.component';
import { IntroComponent} from './static/intro/intro.component';

const routes: Routes = [
  { path: 'introduction', component: IntroComponent},
  { path: '',   redirectTo: '/introduction', pathMatch: 'full' },
  { path: 'book',         component: PartyComponent },
  { path: 'static',       component: StaticComponent },
  { path: 'dacc',         component: DaccComponent },
  { path: 'instruments',  component: InstrumentsComponent},
  { path: 'login',        component: LoginComponent },
  { path: 'company',      component: HomeComponent},
  { path: 'taskexplorer', component: TaskexplorerComponent},
  { path: 'charges',      component: ChargesComponent},
  { path: 'home', component: HomeComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
