import { Component, Output, EventEmitter, OnInit, Input, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'app-menubar',
  templateUrl: './menubar.component.html',
  styleUrls: ['./menubar.component.scss']
})
export class MenubarComponent implements OnInit {
  @Output() notifyParentSave: EventEmitter<any> = new EventEmitter();
  @Output() notifyParentRefresh: EventEmitter<any> = new EventEmitter();
  @Output() notifyParentDelete: EventEmitter<any> = new EventEmitter();

  @Input() public customTitle: string;

  constructor() { }
  onSave() {
    this.notifyParentSave.emit();
  }

  onDelete() {
    this.notifyParentDelete.emit();
  }

  onRefresh() {
    this.notifyParentRefresh.emit();
  }

  onClickAdd() {
    console.log('Add emitter is sent');
    this.onSave();
  }
  onClickRefresh() {
    console.log('Refresh emitter is sent');
    this.onRefresh();
  }
  onClickDelete() {
    console.log('Delete emitter is sent');
    this.onDelete();
  }
  ngOnInit(): void { }

}
