import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { Observable } from 'rxjs';


@Component({
  selector: 'app-home-form',
  templateUrl: './home-form.component.html',
  styleUrls: ['./home-form.component.scss']
})
export class HomeFormComponent implements OnInit {

  formGroup: FormGroup;
  titleAlert = 'This field is required';
  post: any = '';

  constructor(private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.createForm();
    this.setChangeValidate();
  }

  createForm() {
    this.formGroup = this.formBuilder.group({
       Status: [null, Validators.required],
       Host: [null, Validators.required],
       CompanyName: [null, Validators.required],
       Reference: [null, Validators.required],
       ReferenceParty: [null, Validators.required],
       ReferenceSNAM: [null, Validators.required],
       InternalNarrativeEnglish: [null, Validators.required],
       NarrativeJapanese: [null, Validators.required],
       NarrativeBaseCcy: [null, Validators.required],
       PurgeDays: [null, Validators.required],
       Location: [null, Validators.required],
       CountryCode: [null, Validators.required],
       HolidayType: [null, Validators.required],
       TimezoneOffset: [null, Validators.required],
       BICCode: [null, Validators.required],
       JASDECRef: [null, Validators.required],
       JSDARef: [null, Validators.required],
       TSERef: [null, Validators.required],
       OSERef: [null, Validators.required],
       BOJRef: [null, Validators.required],
       JGBNetting: [null, Validators.required],
       ACGL: [null, Validators.required],
       DBAgent: [null, Validators.required],
       BICJGBCC: [null, Validators.required],
       AgentBICNonJGBCC: [null, Validators.required],
       validate: ''
    });
  }

  setChangeValidate() {
    this.formGroup.get('validate').valueChanges.subscribe(
      (validate) => {
        if (validate === '1') {
          this.formGroup.get('name').setValidators([Validators.required, Validators.minLength(3)]);
          this.titleAlert = 'You need to specify at least 3 characters';
        } else {
          this.formGroup.get('name').setValidators(Validators.required);
        }
        this.formGroup.get('name').updateValueAndValidity();
      }
    );
  }

  get name() {
    return this.formGroup.get('name') as FormControl;
  }

  checkPassword(control) {
    const enteredPassword = control.value;
    const passwordCheck = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.{8,})/;
    return (!passwordCheck.test(enteredPassword) && enteredPassword) ? { requirements: true } : null;
  }

  checkInUseEmail(control) {
    // mimic http database access
    const db = ['tony@gmail.com'];
    return new Observable(observer => {
      setTimeout(() => {
        const result = (db.indexOf(control.value) !== -1) ? { alreadyInUse: true } : null;
        observer.next(result);
        observer.complete();
      }, 4000);
    });
  }

  getErrorEmail() {
    return this.formGroup.get('email').hasError('required') ? 'Field is required' :
      this.formGroup.get('email').hasError('pattern') ? 'Not a valid emailaddress' :
        this.formGroup.get('email').hasError('alreadyInUse') ? 'This emailaddress is already in use' : '';
  }

  getErrorPassword() {
    return this.formGroup.get('password').hasError('required') ? 'Field is required (at least eight characters, one uppercase letter and one number)' :
      this.formGroup.get('password').hasError('requirements') ? 'Password needs to be at least eight characters, one uppercase letter and one number' : '';
  }

  onSubmit(post) {
    this.post = post;
  }
}
