import {Apollo, QueryRef} from 'apollo-angular';
import { MenubarComponent } from '../menubar/menubar.component';

import {
  Component,
  OnInit,
  Input,
  ViewEncapsulation,
  OnChanges,
  SimpleChanges,
  DoCheck,
  AfterContentInit,
  AfterContentChecked,
  AfterViewInit,
  AfterViewChecked,
  OnDestroy,
  ViewChild,
  ElementRef,
  ContentChild
} from '@angular/core';

import gql from 'graphql-tag';

const party = gql`
  query {
    party {
      party_ref
      party_type
      party_long_name
      party_short_name
      party_extra_long_name
      version_no
  }
}
`;

@Component({
  selector: 'app-static',
  template: `
     <app-menubar
      (notifyParentSave)="onSave()"
      (notifyParentRefresh)="onRefresh()"
      (notifyParentDelete)="onDelete()"
      customTitle={{title}}>
     </app-menubar>
     <grid [cols]="cols" [rows]="rows"></grid>
  `,
  styles: ['.toolbar-spacer { flex: 1 1 auto; }']
})


export class StaticComponent implements OnInit, OnDestroy {
  title = 'Static Data Components';
  page = 1;
  rows: any[] = [];
  private query: QueryRef<any>;
  public defaultColDef;
  public autoGroupColumnDef;

  cols = [
    {headerName : 'Party Ref', field: 'party_ref'},
    {headerName : 'Type', field: 'party_type'},
    {headerName : 'Long Name', field: 'party_long_name'},
    {headerName : 'Short Name', field: 'party_short_name'},
    {headerName : 'Full Name', field: 'party_extra_long_name'},
    {headerName : 'Version', field: 'version_no'},
  ];

  constructor(private apollo: Apollo) {}

  onSave() {
    console.log('onSave: Run mutation to save data ...');
  }

  onRefresh() {
      console.log('onRefresh: Run query to refresh data ...');
      this.update();
    }

  onDelete() {
    console.log('onDelete: Run mutation to delete data ...');
  }


  ngOnDestroy() {
    console.log('ngOnDestroy called!');
  }

  ngOnInit() {
    this.query = this.apollo.watchQuery({
      query: party,
      variables: { offset: 10 * this.page },
      errorPolicy: 'all'
    });

    this.query.valueChanges.subscribe(result => {
      this.rows = result.data && result.data.party;
    });
  }

  update() {
    console.log('Update static page ....');
    this.query.refetch({ offset: 10 * this.page });
  }

  nextPage() {
    this.page++;
    this.update();
  }

  prevPage() {
    if (this.page > 0) { this.page--; }
    this.update();
  }

  openAddDialog(addData: string) {
    console.log('starting open add dialog...');
    console.log(addData);
  }
}
