import { Component } from '@angular/core';
import { ClientSideRowModelModule } from '@ag-grid-community/client-side-row-model';
import '@ag-grid-community/core/dist/styles/ag-grid.css';
import '@ag-grid-community/core/dist/styles/ag-theme-alpine-dark.css';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'client',
  template: `<ag-grid-angular
    #agGrid
    style="width: 100%; height: 100%;"
    id="myGrid"
    class="ag-theme-alpine-dark"
    [modules]="modules"
    [columnDefs]="columnDefs"
    [defaultColDef]="defaultColDef"
    [rowData]="rowData"
    (gridReady)="onGridReady($event)"
  ></ag-grid-angular>`,
})
export class ClientUpdateComponent {
  private gridApi;
  private gridColumnApi;

  public modules: any[] = [ClientSideRowModelModule];
  public columnDefs;
  public defaultColDef;
  public rowData;

  constructor() {
    this.columnDefs = [
      {
        headerName: 'Name',
        valueGetter(params) {
          return params.data.firstName + ' ' + params.data.lastName;
        },
        valueSetter(params) {
          const fullName = params.newValue;
          const nameSplit = fullName.split(' ');
          const newFirstName = nameSplit[0];
          const newLastName = nameSplit[1];
          const data = params.data;
          if (
            data.firstName !== newFirstName ||
            data.lastName !== newLastName
          ) {
            data.firstName = newFirstName;
            data.lastName = newLastName;
            return true;
          } else {
            return false;
          }
        },
      },
      {
        headerName: 'A',
        field: 'a',
      },
      {
        headerName: 'B',
        valueGetter(params) {
          return params.data.b;
        },
        valueSetter(params) {
          if (params.data.b !== params.newValue) {
            // tslint:disable-next-line: radix
            params.data.b = parseInt(params.newValue);
            return true;
          } else {
            return false;
          }
        },
      },
      {
        headerName: 'C.X',
        valueGetter(params: { data: { c: { x: any; }; }; }) {
          if (params.data.c) {
            return params.data.c.x;
          } else {
            return undefined;
          }
        },
        valueSetter(params) {
          if (!params.data.c) {
            params.data.c = {};
          }
          params.data.c.x = params.newValue;
          return true;
        },
      },
      {
        headerName: 'C.Y',
        valueGetter(params) {
          if (params.data.c) {
            return params.data.c.y;
          } else {
            return undefined;
          }
        },
        valueSetter(params) {
          if (!params.data.c) {
            params.data.c = {};
          }
          params.data.c.y = params.newValue;
          return true;
        },
      },
    ];
    this.defaultColDef = {
      flex: 1,
      resizable: true,
      editable: true,
    };
    this.rowData = createRowData();
  }

  onGridReady(params) {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
  }
}

function createRowData() {
  const rowData = [];
  const firstNames = ['Niall', 'John', 'Rob', 'Alberto', 'Bas', 'Dimple', 'Sean'];
  const lastNames = [
    'Pink',
    'Black',
    'White',
    'Brown',
    'Smith',
    'Smooth',
    'Anderson',
  ];
  for (let i = 0; i < 100; i++) {
    rowData.push({
      a: Math.floor(Math.random() * 100),
      b: Math.floor(Math.random() * 100),
      firstName: firstNames[i % firstNames.length],
      lastName: lastNames[i % lastNames.length],
    });
  }
  return rowData;
}
