import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { Subscription } from 'rxjs';
import { AgGridAngular } from '@ag-grid-community/angular';
import { ClientSideRowModelModule} from '@ag-grid-community/all-modules';
import { CloseScrollStrategy } from '@angular/cdk/overlay';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'grid',
  template: `
  <div class="example-header">
        Selection:
        <span id="selectedRows"></span>
  </div>
   <ag-grid-angular
      style="width: 100%; height: 100%;"
      class="ag-theme-alpine-dark"
      [defaultColDef]="defaultColDef"
      [autoGroupColumnDef]="autoGroupColumnDef"
      [enableRangeSelection]="true"
      [animateRows]="true"
      domLayout='autoHeight'
      [rowData]=rows
      [columnDefs]=cols
      (rowClicked)='onRowClicked($event)'
      (cellClicked)='onCellClicked($event)'
      (selectionChanged) = 'onSelectionChanged($event)'
      (cellEditingStarted) = 'onCellEditingStarted($event)'
      >
  </ag-grid-angular>
  `,
   styles: ['h1 { font-weight: normal; }']
})

export class GridComponent implements OnInit {
  @ViewChild('agGrid') agGrid: AgGridAngular;
  page = 0;
  private gridApi;
  private gridColumnApi;
  public defaultColDef;
  public autoGroupColumnDef;

  @Input() public cols: any[];
  @Input() public rows: any[];

  onRowClicked(event: any) {
    const data = event.node.data;
    const myJSON = JSON.stringify(data);
    console.log('JSON Object', myJSON);
  }

  onCellClicked(event: any) {
    const data = event.node.data;
    const myJSON = JSON.stringify(data);
    console.log('OnCellClicked: JSON Object', myJSON);
   }

  onSelectionChanged(event: any)
  {
    console.log('onSelectionChanged', event);
    const selectedRows = event.api.getSelectedNodes();
    console.log('onSelectionChanged', selectedRows);
  }

  ngOnInit() {
    console.log('Grid initializing ...');

    this.defaultColDef = {
        flex: 1,
        minWidth: 110,
        filter: true,
        sortable: true,
        resizable: true,
        editable: true,
   };

    this.autoGroupColumnDef = { minWidth: 50 };
    console.log('Auto grid group initialized ... ');
  }

  onBtStopEditing() {
    this.gridApi.stopEditing();
  }

  onCellEditingStarted(event: any) {
    console.log('Started editing: now');
  }

  onGridReady(params){
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    params.api.sizeColumnsToFit();
  }
}
